article {
  background: white;
  border-radius: 0.4rem;
  .container {
    width: 90%;
    .va-main-img {
      border-radius: 50%;
      height: 45px;
      width: 45px;
    }
    h4 {
      font-size: 1.1rem;
    }
    h6 {
      font-size: 0.85rem;
      margin: 0 !important;
    }
    .bi-share {
      cursor: pointer;
    }
    .modal-body {
      .bi {
        font-size: 30px;
        cursor: pointer;
      }
      .bi-facebook {
        color: #3b5998;
      }
      .bi-twitter {
        color: #00acee;
      }
      .bi-linkedin {
        color: #0e76a8;
      }
    }
    .main-content {
      h1 {
        font-size: 1.4rem;
      }
      p {
        line-height: 30px;
        font-size: 0.95rem;
      }
      li {
        font-size: 0.95rem;
      }
    }
  }
}

@media (max-width: 768px) {
  .main-content {
    h1 {
      font-size: 1rem !important;
    }
    p {
      font-size: 0.9rem !important;
    }
  }
}
